import React, {FC} from 'react';
import {Route, Routes as RoutesSource} from 'react-router-dom';

import {Film, Main, NotFound, Search, Settings} from "../../pages";


export const Routes: FC = () => {

  return (
    <RoutesSource>
      <Route path='/Pixema' element={<Main />}/>
      <Route path='/Pixema/search' element={<Search />}/>
      <Route path='/Pixema/film/:imdbID' element={<Film />}/>
      <Route path='/Pixema/settings' element={<Settings />}/>
      <Route path='*' element={<NotFound />}/>
    </RoutesSource>
  );
};
