import {FC} from 'react';
import {Navigate, Route, Routes as RoutesSource} from 'react-router-dom';

import {NotFound, SignIn, SignUp} from "../../pages";

export const AuthRoutes: FC = () => {
  return (
    <RoutesSource>
      <Route path='/Pixema' element={<Navigate to="/Pixema/signIn" />}/>
      <Route path='/Pixema/search' element={<Navigate to="/Pixema/signIn" />}/>
      <Route path='/Pixema/film/:imdbID' element={<Navigate to="/Pixema/signIn" />}/>
      <Route path='/Pixema/settings' element={<Navigate to="/Pixema/signIn" />}/>
      <Route path='/Pixema/signIn' element={<SignIn />}/>
      <Route path='/Pixema/signUp' element={<SignUp />}/>
      <Route path='*' element={<NotFound />}/>
    </RoutesSource>
  );
};
